@import url("https://cdnjs.cloudflare.com/ajax/libs/font-awesome/6.3.0/css/all.min.css");
body {
  color: #213f7d;
  font: 100% "Avenir Next LT Pro", sans-serif;
  margin: 0;
  padding: 0; }

.backLink {
  color: #545f7d;
  text-decoration: none;
  letter-spacing: 0.1rem;
  margin-bottom: 1em;
  display: block; }

.subheader {
  display: block; }

.subheader__right {
  margin-top: 1em; }

.actionUserBtn {
  text-transform: uppercase;
  padding: 0.5em;
  background-color: #e5e5e5;
  border-radius: 5px;
  font-size: 0.875rem; }

.actionUserBtn + .actionUserBtn {
  margin-left: 1em; }

.actionUserBtnDanger {
  border: 1px solid #e4033b;
  color: #e4033b; }

.actionUserBtnSuccess {
  border: 1px solid #39cdcc;
  color: #39cdcc; }

.sectionHeader,
.sectionBody {
  margin: 1.5em;
  padding: 1em 1em 0 1em;
  background-color: white;
  border-radius: 6px; }

.sectionHeaderContent {
  display: block; }

.headerContentUser {
  display: flex;
  justify-content: flex-start;
  align-items: center; }

.avatar {
  width: 40%; }

.avatar img {
  border-radius: 50%; }

.userdetail {
  margin-left: 1em; }

.userdetail p,
.userdetail h3 {
  margin: 0; }

.headerContentAmout {
  margin-top: 1em; }

.headerContentAmout h3,
.headerContentAmout p {
  margin: 0; }

.userdetail p,
.headerContentAmout p {
  margin-top: 0.5em; }

.headerTabs {
  width: auto;
  max-width: 295px;
  margin-top: 2em;
  overflow-x: auto; }

.headerTabsList {
  width: 600px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  list-style: none;
  padding: 0;
  margin: 0; }

.headerTabsItem {
  color: #545f7d;
  padding-bottom: 0.5rem; }

.itemSelected {
  color: #39cdcc;
  border-bottom: 2px solid #39cdcc;
  padding-left: 1rem;
  padding-right: 1rem; }

.sectionBody {
  padding-bottom: 1em; }

.sectionBodyRow {
  border-bottom: 1px solid #e5e5e5; }

.sectionBodyRow:last-child {
  border-bottom: none; }

.sectionBodyRow h3,
.sectionBodyLabel {
  margin-bottom: 0; }

.sectionBodyCategory {
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap; }

.sectionBodyCol {
  flex: 1 1 130px; }

.sectionBodyLabel {
  color: #545f7d;
  text-transform: uppercase;
  font-weight: 400;
  font-size: 1rem; }

.sectionBodyItem {
  margin-top: 0.5em;
  color: #545f7d;
  font-weight: 500;
  font-size: 1rem; }

@media (min-width: 768px) {
  .headerTabs {
    max-width: 442px; } }

@media (min-width: 1024px) {
  .subheader {
    display: flex;
    justify-content: space-between;
    align-items: center; }
  .actionUserBtn + .actionUserBtn {
    margin-right: 0.4em; }
  .sectionHeaderContent {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    gap: 3rem; }
  .headerContentTier p {
    margin: 0;
    margin-bottom: 0.5em; }
  .headerContentAmout {
    margin-top: 0; }
  .headerContentTier,
  .headerContentAmout {
    border-left: 1px solid #e5e5e5;
    padding-left: 2em;
    padding-top: 1em;
    padding-bottom: 1em; }
  .headerTabs {
    max-width: 100%; }
  .headerTabsList {
    width: auto; }
  .sectionBodyCol {
    flex: 0 1 220px; } }
